import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  computed,
  DestroyRef,
  HostBinding,
  inject,
  OnInit,
  signal,
  TemplateRef,
  ViewChild,
  effect,
} from '@angular/core';
import {
  ActivatedRoute,
  Data,
  NavigationEnd,
  Router,
  RouterModule,
} from '@angular/router';

import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { filter, map } from 'rxjs';

import {
  TabConfig,
  TypedTabConfig,
} from '../../../../../shared/models/tabs.model';
import { DynamicTabPanelComponent } from '../../../../../shared/components/tabs/dynamic-tab-panel/dynamic-tab-panel.component';
import { EntityDetailsService } from '../../../services/entity/entity-details.service';
import { isDefined } from '../../../../../shared/utils';
import {
  DISTRIBUTOR_ADMIN_FEATURES,
  DistributorFeatureId,
} from '../distributor-administration.config';
import { PermissionService } from '../../../../../core/services/permission.service';
import { QAToolsService } from '../../../../../core/services/qa-tools.service';

@Component({
  selector: 'app-distributor-container',
  imports: [DynamicTabPanelComponent, RouterModule],
  templateUrl: './distributor-container.component.html',
  styleUrl: './distributor-container.component.scss',
  host: {
    class: '!flex !flex-col !h-full !w-full !overflow-hidden',
  },
})
export class DistributorContainerComponent implements AfterViewInit, OnInit {
  @ViewChild('main') mainTemplate!: TemplateRef<unknown>;
  readonly qaTools = inject(QAToolsService);
  private readonly baseDistributorTabs: TypedTabConfig<DistributorFeatureId>[] =
    [
      { id: 'details', label: 'Details', route: 'details', level: 0 },
      { id: 'vendors', label: 'Vendors', route: 'vendors', level: 0 },
      {
        id: 'providers',
        label: 'Providers',
        level: 0,
        children: [
          {
            id: 'distributor-device-providers',
            label: 'Device Providers',
            route: 'providers/distributor-device-providers',
            level: 1,
          },
          {
            id: 'distributor-email-providers',
            label: 'Email Providers',
            route: 'providers/distributor-email-providers',
            level: 1,
          },
        ],
      },
      {
        id: 'profiles',
        label: 'Profiles',
        level: 0,
        children: [
          {
            id: 'distributor-company-groups',
            label: 'Vendor Groups',
            route: 'profiles/distributor-company-groups',
            level: 1,
          },
          {
            id: 'distributor-road-profiles',
            label: 'Road Profiles',
            route: 'profiles/distributor-road-profiles',
            level: 1,
          },
          {
            id: 'distributor-themes',
            label: 'Themes',
            route: 'profiles/distributor-themes',
            level: 1,
          },
        ],
      },
      {
        id: 'accounts',
        label: 'Accounts',
        level: 0,
        children: [
          {
            id: 'distributor-users',
            label: 'Users',
            route: 'accounts/distributor-users',
            level: 1,
          },
          {
            id: 'distributor-user-roles',
            label: 'User Roles',
            route: 'accounts/distributor-user-roles',
            level: 1,
          },
        ],
      },
    ];
  readonly distributorTabs = computed(() => {
    return this.filterTabsByPermissions(this.baseDistributorTabs);
  });

  readonly detailsService = inject(EntityDetailsService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly router = inject(Router);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly destroyRef = inject(DestroyRef);
  private readonly permissionService = inject(PermissionService);
  readonly isContentScrollable = signal<boolean>(false); // Signal to control styling

  constructor() {
    // Recalculate tabs when QA profile changes
    effect(() => {
      this.qaTools.activePersona();
      // The distributorTabs computed signal will automatically recalculate
    });
  }

  @HostBinding('class.content-scrollable-inactive')
  get contentScrollableActive(): boolean {
    return !this.isContentScrollable();
  }

  ngAfterViewInit() {
    this.applyTemplateToTabs(this.baseDistributorTabs, this.mainTemplate);
    this.cdr.detectChanges();
  }

  private applyTemplateToTabs(
    tabs: TabConfig[],
    template: TemplateRef<unknown>,
  ) {
    for (const tab of tabs) {
      tab.template = template;
      if (tab.children) {
        this.applyTemplateToTabs(tab.children, template);
      }
    }
  }

  onTabChange() {
    this.detailsService.closeDetailsPanel();
  }

  ngOnInit(): void {
    // Function to get scrollable state from route
    const getScrollableState = (route: ActivatedRoute): boolean => {
      let currentRoute = route;
      while (currentRoute.firstChild) {
        currentRoute = currentRoute.firstChild;
      }
      const routeData: Data = currentRoute.snapshot.data;
      if (
        isDefined(routeData) &&
        typeof routeData['nestedContentScrollable'] === 'boolean'
      ) {
        return routeData['nestedContentScrollable'];
      }
      return false; // Default to false
    };

    // Set initial state
    this.isContentScrollable.set(getScrollableState(this.activatedRoute));
    this.cdr.detectChanges(); // Ensure initial class binding is applied

    // Subscribe to subsequent navigation events
    this.router.events
      .pipe(
        filter(
          (event): event is NavigationEnd => event instanceof NavigationEnd,
        ),
        map(() => getScrollableState(this.activatedRoute)), // Reuse the logic
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((scrollableValue: boolean) => {
        if (this.isContentScrollable() !== scrollableValue) {
          this.isContentScrollable.set(scrollableValue);
          this.cdr.detectChanges(); // Explicitly trigger change detection if needed
        }
      });
  }

  private filterTabsByPermissions(
    tabs: TypedTabConfig<DistributorFeatureId>[],
  ): TypedTabConfig<DistributorFeatureId>[] {
    return tabs
      .map((tab) => {
        const config = DISTRIBUTOR_ADMIN_FEATURES[tab.id];
        const hasPermission = this.permissionService.canAccess(
          'permissions' in config ? config.permissions : undefined,
        );

        // If tab has no permission, exclude it
        if (!hasPermission) {
          return null;
        }

        // If tab has children, recursively filter them
        if (tab.children !== undefined && tab.children.length > 0) {
          const filteredChildren = this.filterTabsByPermissions(tab.children);

          // If all children are hidden, hide parent too
          if (filteredChildren.length === 0) {
            return null;
          }

          // Return tab with filtered children
          return {
            ...tab,
            children: filteredChildren,
          };
        }

        return tab;
      })
      .filter(
        (tab): tab is TypedTabConfig<DistributorFeatureId> => tab !== null,
      );
  }
}
