import { CanActivateFn, Router } from '@angular/router';
import { inject } from '@angular/core';
import { PermissionService } from '../../../core/services/permission.service';
import {
  DISTRIBUTOR_ADMIN_FEATURES,
  DistributorFeatureId,
} from '../components/distributors/distributor-administration.config';
import { isNullOrUndefined } from '../../../shared/utils';

/**
 * Guard for distributor admin routes using dynamic :type parameter.
 * Uses DISTRIBUTOR_ADMIN_FEATURES to check permissions with distributor context.
 */
export const distributorTypePermissionGuard: CanActivateFn = async (route) => {
  const permissionService = inject(PermissionService);
  const router = inject(Router);
  const type = route.paramMap.get('type') as DistributorFeatureId | null;

  if (isNullOrUndefined(type)) {
    return true;
  }

  if (!(type in DISTRIBUTOR_ADMIN_FEATURES)) {
    return true;
  }

  const featureConfig = DISTRIBUTOR_ADMIN_FEATURES[type];
  const permissions =
    'permissions' in featureConfig ? featureConfig.permissions : undefined;

  if (!permissionService.canAccess(permissions, 'distributor')) {
    await router.navigate(['/unauthorized'], {
      queryParams: {
        requiredPermission: JSON.stringify(permissions ?? 'no_permission'),
      },
    });
    return false;
  }

  return true;
};
